.headComponent {
  display: flex;
  justify-content: space-between;

  height: 60px;
  padding: 0 20px;

  border-bottom: 1px solid rgb(60, 60, 255);
  box-shadow: 0px 0px 2px rgb(60, 60, 255);
}

.infoUser {
  display: flex;
  align-items: center;
}
.user-name {
  color: #606060;
}
.label {
  display: flex;
  align-items: center;
}

input {
  text-align: center;
}
.user-ava {
  box-sizing: border-box;
  height: 35px;
  border-radius: 150px;
}

@media (orientation: portrait) {
  .headComponent {
    height: 130px;
    font-size: 30px;
  }
  .user-name {
    display: none;
  }

  .user-ava {
    height: 90px;
  }
  .logo {
    height: 100px;
  }
}
